<template>
    <div>
        select model: <select name="" id="spineModelSelect" v-model="selectedModel"
        ><option v-for="i in modelNames.length" :key="i" :value="modelNames[i-1]">{{ modelNames[i-1] }}</option></select
        >
        <my-spine :skelName="selectedModel"></my-spine>
    </div>
</template>

<script>
    import MySpine from "./MySpine.vue"
    import axios from 'axios'
    export default {
        name: "MySpineController",
        components:{MySpine},
        data(){
            return{
                selectedModel:"ximu_3",
                modelNames:[]
            }
        },
        created(){
            axios.get("http://pic.ggemo.com/al_spines/sourceList.json").then((response)=> {
                console.log(response);
                this.modelNames = response.data;
            })
        }
    }
</script>

<style scoped>

</style>



