<template>
    <div ref="spine" id="mySpineHolder">
        select animation: <select name="" id="spineSelect" v-model="selectedAnimation"
            ><option v-for="i in animations.length" :key="i" :value="i-1">{{ animations[i-1].name }}</option></select
        >
    </div>
</template>

<script>
import 'pixi-spine';
import {SD} from '../js/sd.js';
import * as PIXI from 'pixi.js';
import {SkeletonBinary} from '../js/SkeletonBinary.js';

export default {
    name: 'MySpine',
    props: ['skelName'],
    data() {
        return {
            basePath:"http://pic.ggemo.com/al_spines/",
            spineData: [],
            animations: [],
            selectedAnimation:""
        };
    },
    watch:{
        selectedAnimation:function (newName,oldName) {
            this.changeAnimation(newName)
        },
        skelName:function (newName,oldName) {
            this.load(newName);
        }
    },
    created() {
        window.v = this;
        this.PIXI = new PIXI.Application(512, 642, {
            transparent: true,
        });
    },
    mounted() {
        console.log(this.$refs.spine);
        this.PIXI.view.id="showSD"
        this.$refs.spine.appendChild(this.PIXI.view);
        this.load(this.skelName);
    },
    methods: {
        load: function(skelName) {
            let basePath = this.basePath + skelName
            if (!this.spineData[skelName]) {
                this.loader = new PIXI.loaders.Loader(basePath);
                let skelpath = skelName + '.skel';
                let atlaspath = skelName + '.atlas';
                let texpath = skelName + '.png';
                this.loader.add(skelName + '_atlas', atlaspath, {
                    type: 'atlas',
                });
                this.loader.add(skelName + '_skel', skelpath, {
                    xhrType: 'arraybuffer',
                });
                this.loader.add(skelName + '_tex', texpath);

                this.loader.load((loader, resources) => {
                    let skelBin = new SkeletonBinary();
                    skelBin.data = new Uint8Array(resources[skelName + '_skel'].data);

                    skelBin.initJson();
                    let rawSkeletonData = skelBin.json;

                    let rawAtlasData = resources[skelName + '_atlas'].data;
                    let spineAtlas = new PIXI.spine.core.TextureAtlas(rawAtlasData, function(line, callback) {
                        callback(PIXI.BaseTexture.from(skelName + '_tex'));
                    });
                    let spineAtlasLoader = new PIXI.spine.core.AtlasAttachmentLoader(spineAtlas);
                    let spineJsonParser = new PIXI.spine.core.SkeletonJson(spineAtlasLoader);
                    let skeletonData = spineJsonParser.readSkeletonData(rawSkeletonData);
                    this.spineData[skelName] = skeletonData;
                    this.changeCanvas(skeletonData);
                    return skeletonData;
                });
            } else {
                this.changeCanvas(this.spineData[skelName]);
                return this.spineData[skelName];
            }
        },
        changeCanvas: function(skeletonData) {
            let viewer = this;
            viewer.PIXI.stage.removeChildren();
            viewer.spine = new PIXI.spine.Spine(skeletonData);
            this.animations = viewer.spine.spineData.animations;;
            viewer.spine.state.setAnimation(0, this.animations[0].name, true);
            viewer.PIXI.stage.addChild(viewer.spine);
            viewer.spine.position.set(viewer.PIXI.view.width * 0.5, viewer.PIXI.view.height * 0.8);
        },
        changeAnimation: function(num) {
            let name = this.spine.spineData.animations[num].name;
            this.spine.state.setAnimation(0, name, true);
        },
    },
};
</script>

<style>
select {
    width: 150px;
    height: 30px;

    top: 0;
    left: 0;
    z-index: 999;
    margin-top: 5px;
    margin-left: 5px;
}
#showSD{
    float: left;
}
    #mySpineHolder{
        width:512px;
        height: 670px;
        padding: 0;
    }
</style>



